.course-card {
    border: 1px solid #ccc;
    padding: 15px;
    margin: 10px;
    background-color: #f9f9f9;
    border-radius: 5px;
    border-color: rgba(68, 228, 47, 0.652);
    box-shadow: 0px 2px 4px rgba(68, 228, 47, 0.652);
    width: auto;
  }
  
  .course-card h3 {
    margin: 0;
  }
  
  .course-card button {
    padding: 5px 10px;
    cursor: pointer;
    border-radius: 3px violet;
    align-self: center;
  }
  
  
  .course-details {
    display: flex;
    justify-content: space-between;
  }
  
  .description {
    flex: 2;
    padding-right: 20px;
  }
  
.info {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}
  
  .info p {
    margin: 5px 0;
  }
  