/* createcourse.css */

/* Style for the container */
.create-course-container {
  width: 80%;
  margin: 0 auto;
}

/* Style for headings */
.create-course-container h2 {
  font-size: 24px;
  margin-bottom: 16px;
}

.create-course-container h3 {
  font-size: 18px;
  margin-top: 16px;
}

/* Style for input fields */
.create-course-container input[type="text"],
.create-course-container input[type="number"]{
  width: 20%;
  padding: 8px;
  margin-bottom: 16px;
  border: 1px solid #ccc;
  border-radius: 4px;
}


.create-course-container textarea {
  width: 100%;
  padding: 8px;
  margin-bottom: 16px;
  border: 1px solid #ccc;
  border-radius: 4px;
  height: 50px;
}

/* Style for buttons */
.create-course-container button {
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 4px;
  padding: 8px 16px;
  cursor: pointer;
}

.create-course-container button:hover {
  background-color: #0056b3;
}

/* Style for lists */
.create-course-container ul {
  list-style-type: none;
  padding: 0;
}

.create-course-container ul li {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 8px;
}

.create-course-container ul li button {
  background-color: none;
  color: white;
  border: none;
  border-radius: 5px;
  padding: 4px 8px;
  cursor: pointer;
}

.create-course-container ul li button:hover {
  background-color: #c82333;
}
