/* searchbar.css */

.search-bar {
    display: flex;
    flex: 1;
    align-items: center;
    padding: 20px;
    width: 100%;
}

.input{
  width: 100%;
  height: 35px;
}

.input-container {
  margin-top: 7px;
  width: 50%;
  align-items: center;
}

.input-label {
  margin-right: 10px;
}

.search-btn{
  height: 49px;
  border-radius: 5px;
  text-align: center;
  width: 70px;
}

.add-new-course-button{
  text-align: center;
  height: 39px;
  justify-content: flex-end;
  cursor: pointer;
}

.rightside {
  display: flex;
  align-items: center;
  padding-left: 30px;
}