/* Courses.css */

.courses-list {
    display: flex;
    flex-direction: column;
    align-items: center;
    border-radius: 5px blue;
  }
  
  .search-bar-container {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
  }
  
  .add-new-course-button {
    padding-right: 10px;
    border-radius: 3px violet;
    color: none;
    margin-right: 10px;
    height: 39px;
    cursor: pointer;
  }

.searchbar{
  display: flex;
  justify-content: space-between;
  width: 100%;
}
  