/* Styles for the header container */
.header-container {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 20px;
    background-color: #f0f0f0;
  }
  
  /* Styles for the heading */
  .heading {
    font-size: 24px;
    margin: 0;
    padding-right: 20px;
}
  /* Apply styles to the button */
  .custom-button {
    background-color: #f431b3;
    color: #fff;
    border: none;
    padding: 10px 20px;
    font-size: 16px;
    cursor: pointer;
  }
  
  .custom-button:hover {
    background-color: #0056b3;
  }
  

  .right-side {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }